function toggleMenu(e) {
  e.preventDefault();

  var bodyEl = document.querySelector('body');
  if (bodyEl.classList.contains('smallscreen-menu--active')) {
    bodyEl.classList.remove('smallscreen-menu--active');
  } else {
    bodyEl.classList.add('smallscreen-menu--active');
  }
}

function primaryNav() {
  var t = document.querySelector('#smallscreen-menu');
  var smallScreenNav = document.importNode(t.content, true).querySelector('div');
  smallScreenNav.className = 'smallscreen-menu';
  document.body.appendChild(smallScreenNav);

  var menuButton = document.querySelectorAll('.js-smallscreen-menu--toggle');
  menuButton.forEach(function (el) {
    el.addEventListener('click', toggleMenu);
  });
}

module.exports = primaryNav;

